.Input_wrap {
  width: 100%;
  margin-top: 50px;
  position: relative;
}
.Input_wrap input {
  text-align: left;
  width: 100%;
  height: 55px;
  border-radius: 5px;
  border: 1px solid rgb(170, 170, 170);
  box-sizing: border-box;
  padding: 0 15px;
}
.Input_wrap input:focus {
  outline: none;
  border-color: black;
}
.Input_wrap input::-moz-placeholder {
  text-align: left;
  margin-left: 0px;
  color: rgb(170, 170, 170);
}
.Input_wrap input::placeholder {
  text-align: left;
  margin-left: 0px;
  color: rgb(170, 170, 170);
}
.Input_wrap .Input_label {
  position: absolute;
  font-size: small;
  background-color: #f8f8f8;
  height: 2px;
  display: flex;
  align-items: center;
  top: 0px;
  left: 29px;
  padding: 0 4px;
}

.inputRadio_stack {
  height: 130px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  min-width: 300px;
}
.inputRadio_stack .inputRadio_wrapper {
  display: flex;
  position: relative;
  padding-left: 32px;
  align-items: center;
}
.inputRadio_stack .inputRadio_wrapper input {
  height: 20px;
  width: 20px;
  position: absolute;
  left: -6px;
  top: 0;
  accent-color: #05a3b4;
}
.inputRadio_stack .inputRadio_wrapper input[type=radio] {
  display: none;
}
.inputRadio_stack .inputRadio_wrapper input[type=radio] + label {
  display: inline-block;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  border: 2px solid black;
}
.inputRadio_stack .inputRadio_wrapper input[type=radio]:checked + label {
  background-color: black;
}
.inputRadio_stack .inputRadio_wrapper .Text {
  margin: 0;
}

.lds-ring {
  display: inline-block;
  position: relative;
  width: 40px;
  height: 40px;
  margin: 7px;
}
.lds-ring div {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 32px;
  height: 32px;
  margin: 4px;
  border: 4px solid #ffffff;
  border-radius: 50%;
  animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: #00bdd3 transparent transparent transparent;
}
.lds-ring div:nth-child(1) {
  animation-delay: -0.45s;
}
.lds-ring div:nth-child(2) {
  animation-delay: -0.3s;
}
.lds-ring div:nth-child(3) {
  animation-delay: -0.15s;
}

@keyframes lds-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
