.inputRadio_stack {
  height: 130px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  min-width: 300px;

  .inputRadio_wrapper {
    display: flex;
    position: relative;
    padding-left: 32px;
    align-items: center;

    input {
      height: 20px;
      width: 20px;
      position: absolute;
      left: -6px;
      top: 0;
      accent-color: #05a3b4;
    }
    input[type="radio"] {
      display: none;
      
      & + label {
        display: inline-block;
        width: 16px;
        height: 16px;
        border-radius: 50%;
        border: 2px solid black;
      }
      
      &:checked + label {
        background-color: black;
      }
    }
    
    
    .Text {
      margin: 0;
    }
  }
}
